.appBar {
    background-color: #fff!important;
    color: black!important;
    -webkit-box-shadow: 0px 2px 7px -1px rgba(0, 0, 0, 0.1) !important;
            box-shadow: 0px 2px 7px -1px rgba(0, 0, 0, 0.1) !important;
}

.appBar > div {
    padding-left: 7px;
}

.left img {
    width: 50px;
    height: 50px;
}

.left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 45%;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.appBar img {
    display: block;
}

.searchBar {
    display:inline-block;
    position: relative;
    width: 80%;
    margin-top: -5px;
}

.searchField {
    margin: 0!important;
    width: 100%;
}

.searchField div::before {
    background: #e6eaec;
}

.searchIcon {
    bottom: 8px;
    position: absolute;
    height: 28px!important;
    width: 28px!important;
    color: rgba(0, 0, 0, 0.5);
}

.right {
    position: absolute;
    right: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.right .appsLogo {
    margin-right: 10px;
    min-width: 0;
    padding: 8px 8px;
    border-radius: 3px;
}

.right .appsLogo img {
    width: 22px;
}

.right .topSites {
    margin-right: 20px;
}

.right .topSite {
    /* padding: 8px 8px;
    min-width: 0px!important;
    margin-right: 5px; */
    padding: 8px;
    min-width: 0!important;
    /* box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12); */
    border-radius: 20px;
}

.right .spacing {
    margin: 0 1px;
}

.right .topSite img {
    width: 20px;
}

.appBar .loginButton {
    border-radius: 3px;
    display: inline-block;
    margin-right: 10px;
}

.moreButton {
    color: black!important;
}

.moreButton span div {
    width: 34px;
    height: 34px;
}

.moreIcon {
    width: 30px!important;
    height: 30px!important;
    color: black!important;
}

.popover {
    border-radius: 5px;
}

.popover div:nth-child(2) {
    overflow: visible;
    top: 64px!important;
}

.arrow-up {
    position: absolute;
    width: 0px;
    height: 0px;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-bottom: 15px solid #fff;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    top: -15px;
}

.loadingDiv {
    position: fixed;
    width: 100%!important;
    height: 100%!important;
    left:0;
    top:0;
    z-index: 9001;
}

.loadingDiv::before {
    content: "";
    position: fixed;
    left: 0;
    right: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
    background: rgba(255, 255, 255, 0.75);
}

.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align:center;
        align-items:center;

}

.progress {
    height: 100px!important;
    width: 100px!important;
}

.loading h2 {
    font-weight: 700;
}

.bookmarks {
    color: #b2bcc3;
}


