.addcoin {
	background-color: white;
	width: 500px;
	height: 500px;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	display: -ms-flexbox;
	display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    border-radius: 5px;
}

.addcoin .header {
	height: 12.5%;
	width: 100%;
	top: 0;
	background:  #FFC107;
    border-radius: 5px 5px 0 0;
    position: relative;
}

.addcoin .header h2 {
	margin: 0;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
	color: black;
	margin-left: 20px;
	font-size: 20px;
	position: absolute;
    left: 0;
    font-weight: 300;
}

.addcoin .header .closeButton {
	position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
  	margin-right: 5px;
}

.addcoin form {
	padding: 30px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.addcoin form > * {
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
}

.addcoin .amount {
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;
	margin-right: 10px;
}

.addcoin .tradePrice {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    margin-left: 10px;
}

.addcoin .currency {
	margin-right: 10px;
	margin-top: 20px;
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;
}

.addcoin .priceType {
	margin-left: 10px;
	margin-top: 20px;
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;
}

.addcoin .addCoinButton {
	-ms-flex-item-align: end;
	    align-self: flex-end;
}

.hidden {
	visibility: hidden;
}


