.crypto {
	display: -ms-flexbox;
	display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.crypto .header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	position: relative;
}

.header > div {
	width: 100%
}

.crypto .header .add {
	margin-left: 20px;
	margin-top: 7px;
	position: absolute;
    right: -50px;
}
.crypto .positive {
	color: green;
}

.crypto .negative {
	color: red;
}

.currentPrice {
	text-align: right!important;
}

.crypto .main {
	font-size: 1.1em;
	font-weight: 400;
}

.crypto .subMain {
	color: #999;
}


