.cryptofolio-progress {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	margin-top: 30px;
}

.cards {
	margin-bottom: 30px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 30px;
}

.cards .stat {
	margin: 0 5px;
}

.cards .stat .stat-content {
	padding: 10px 20px;
}

.cards .stat h1, .cryptofolio-progress .stat h2 {
	font-weight: 300;
	text-align: center;
}




